import React, { useEffect, useRef } from 'react';
import './Bubble.css';

function Bubble({ x, y, color }) {
  const bubbleRef = useRef(null);

  useEffect(() => {
    if (bubbleRef.current) {
      bubbleRef.current.style.top = `${y}px`; // Обновляем позицию по Y
    }
  }, [y]); // Перезапускаем при изменении Y

  return (
    <div
      ref={bubbleRef}
      className={`bubble ${color}`}
      style={{ left: x, position: 'absolute', top: 0 }} // Начальная позиция по Y
    ></div>
  );
}

export default Bubble;
